* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: rgba(231, 231, 231, 0.74);
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  background-color: #f55a5a;
  color: white;
}
.header h1 {
  margin-left: 7px;
  text-transform: capitalize;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14.4608px;
  line-height: 18px;
  letter-spacing: -0.075em;
}
.wrap {
  background-color: rgb(231, 231, 231);
  max-width: 1160px;
  margin: 0 auto;
}
.all-content {
  padding-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
}
.main {
  z-index: 1;
  padding: 100px 50px;
  display: flex;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 0px;
}
.content-image {
  padding-right: 29px;
}
.background {
  background-color: #f55a5a;
  width: 125px;
  margin-left: -6px;
  border-radius: 10px;
  height: 173px;
  z-index: -1;
  position: absolute;
}
.content-image img {
  border-radius: 10px;
}
.content h1 {
  padding: 7px 0;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;

  color: #2b283a;
}
.date h2 {
  padding: 9px 0;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 10.24px;
  line-height: 12px;

  color: #2b283a;
}
.content p {
  width: 326px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  line-height: 150%;
  /* or 15px */

  color: #2b283a;
}
.top-c {
  display: flex;
  align-items: center;
}
.top-c span {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  line-height: 12px;
  letter-spacing: 0.17em;

  color: #2b283a;
  padding-left: 5px;
}
.top-c a {
  cursor: pointer;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 10.24px;
  line-height: 12px;
  /* identical to box height */

  text-decoration-line: underline;

  color: #918e9b;
  padding-left: 12px;
}
@media only screen and (max-width: 1170px) {
  .content p {
    width: fit-content;
  }
  .content-image img {
    width: 170px;

    height: 250px;
  }
  .background {
    width: 170px;
    height: 256px;
  }
}
@media only screen and (max-width: 420px) {
  .main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .content {
    text-align: center;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .top-c {
    display: flex;
    justify-content: center;
  }
  .content-image {
    padding: 0;
  }
  .content-image img {
    width: 170px;

    height: 250px;
  }
  .background {
    width: 170px;
    height: 256px;
  }
}
